<template>
  <div class="videoplayer">
    <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
  </div>
</template>

<script>
export default {
  name: "videoplayer",
  data() {
    return {
      playerOptions: {
        playbackRates: [1.0, 2.0, 3.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "video/mp4",
            type: "video/ogg",
            src: "" //url
          }
        ],
        poster:
          "https://cs.xinpianchang.com/uploadfile/article/2019/11/18/5dd247a2dce77.jpeg@960w_540h_1e_1c.jpg", //
        width: document.documentElement.clientWidth,
        notSupportedMessage: "", //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      }
    };
  },
  mounted() {}
};
</script>

<style>
</style>



